<template>
  <div class="collection">
    <div
      v-for="(item, index) in collectViewsArry"
      :key="index"
      @click="toView(item)"
    >
      <p>
        {{ item }}
        <i
          class="el-icon-close"
          @click.stop="cancelCollect(item)"
          style="
            position: absolute;
            right: 0.2rem;
            top: 50%;
            transform: translateY(-50%);
          "
        ></i>
      </p>
      <div>
        <img :src="require('@/assets/collect/' + item + '.png')" alt="" />
      </div>
    </div>
  </div>
</template>
<script>
import { myCollection } from "@/network/home_view";
export default {
  data() {
    return {
      collectViewsArry: [],
    };
  },
  mounted() {
    this.collectViewsArry = JSON.parse(sessionStorage.getItem("collectViews"));
    // const iframe = document.getElementById("iframes");
    // // 处理兼容行问题
    // if (iframe.attachEvent) {
    //   iframe.attachEvent("onload", () => {
    //     document
    //       .getElementById("iframes")
    //       .contentWindow.document.getElementsByClassName(
    //         "header"
    //       )[0].style.display = "none";
    //   });
    // } else {
    //   iframe.onload = () => {
    //     document
    //       .getElementById("iframes")
    //       .contentWindow.document.getElementsByClassName(
    //         "header"
    //       )[0].style.display = "none";
    //   };
    // }
  },
  methods: {
    toView(name) {
      let route = this.$router.options.routes[2].children;
      route.forEach((item) => {
        if (item.meta.title == name) {
          this.$router.push(item.path);
        }
      });
    },
    cancelCollect(collectName) {
      this.myCollectionFun(collectName);
    },
    myCollectionFun(collectName) {
      myCollection(collectName, 1)
        .then((res) => {
          const { content, errmsg, errcode } = res;
          if (errcode == 0) {
            let viewArry = JSON.parse(sessionStorage.getItem("collectViews")); //是否收藏的数组
            let newArry = viewArry.filter((item) => item != collectName);
            this.collectViewsArry = newArry;
            sessionStorage.setItem("collectViews", JSON.stringify(newArry));

            this.$message({
              message: "取消收藏成功",
              type: "success",
            });
          } else {
            this.$message.error(errmsg);
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
<style scoped lang="less">
.collection {
  background-image: url(@/assets/BG2.png);
  background-size: 100% 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: flex-start;
  padding: 0.2rem;
  box-sizing: border-box;
  background-color: rgba(0, 15, 39, 1);
  width: 100%;
  overflow: scroll !important;
}
.collection > div {
  width: 24%;
  // border: 1px solid rgba(34, 175, 237, 0.5);
  border-radius: 0.05rem;
  background: rgba(0, 55, 146, 0.2);
  margin: 0 0.5%;
  cursor: pointer;
  overflow: hidden;
  margin-bottom: 0.2rem;

  > p {
    color: #ffffff;
    line-height: 0.5rem;
    letter-spacing: 0.02rem;
    position: relative;
    background: url("@/assets/powerStation/titlePng.png");
    background-size: 100% 100%;
    text-align: center;
    font-size: 0.2rem;
    font-weight: bold;
  }
}
.collection > div {
  img {
    width: 100%;
    height: 2.54rem;
    transform: scale(1);
    transition: transform 0.5s;
  }
}
.collection > div:hover {
  img {
    transform: scale(1.05);
    transition: transform 0.5s;
  }
}
</style>